import * as React from 'react';
import { observer, inject } from 'mobx-react';
import ExampleStore from '../store/example-store'
import Appbar from '../components/Appbar';
import Container from '@material-ui/core/Container';
import dynamic from 'next/dynamic'

export interface HomeProps {
  store?: {
    exampleStore: ExampleStore
  }
}

export interface HomeState {
  result: string
}

const DynamicComponentWithNoSSR = dynamic(
  () => import('../components/QRScanner'),
  { ssr: false }
)

@inject('store')
@observer
class Home extends React.Component<HomeProps, HomeState> {
  constructor(props: HomeProps) {
    super(props);
    this.state = {
      result: 'No result'
    }
  }

  validURL = (str) => {
    var pattern = new RegExp('^(https?:\\/\\/)?'+ // protocol
      '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|'+ // domain name
      '((\\d{1,3}\\.){3}\\d{1,3}))'+ // OR ip (v4) address
      '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*'+ // port and path
      '(\\?[;&a-z\\d%_.~+=-]*)?'+ // query string
      '(\\#[-a-z\\d_]*)?$','i'); // fragment locator
    return !!pattern.test(str);
  }

  handleScan = (data) => {
    console.log(data)
    if(this.validURL(data)){
      window.open(data, '_blank');
    }

    this.setState({
      result: data
    });
  }
  
  render() {
    const { exampleStore } = this.props.store;
    return (
      <>
        <Appbar />
        <Container maxWidth="sm">
          <>
            <h3>Scan a QR Code</h3>
            <DynamicComponentWithNoSSR onScan={this.handleScan} />
            <p style={{wordWrap: "break-word"}}>{this.state.result}</p>
          </>
        </Container>
      </>
    );
  }
}

export default Home;